  border-radius: 5px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
}

.is-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.hidscroll::-webkit-scrollbar {
